<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import { Clicks } from '../data/Click'
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'cau-be-va-cay-tao',
  data: () => ({
    anh: '../anh/caubevacaytao.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Cậu bé và cây táo - Truyện cổ Andersen',
    meta: [
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Cậu bé và cây táo' },
      { name: 'Description', content: 'Truyện kể về cậu bé và cây táo. Hình ảnh cây táo trong truyện chính là hình ảnh của cha mẹ.Khi chúng ta còn nhỏ, ta được cha mẹ yêu thương che trở. Khi chúng ta lớn lên, ta bỏ cha mẹ mà đi và chỉ quay trở về khi cần sự giúp đỡ của cha mẹ. Nhưng khi chúng ta cần thì cha mẹ luôn dang rộng vòng tay ra đón lấy chúng ta.' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
    denmodau() {
      Clicks.clickmodau();
    },
    denketthuc() {
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

